  <el-menu-item index="1"></el-menu-item>
  <el-submenu index="2">
    <template slot="title"></template>
    <el-menu-item index="2-1">1</el-menu-item>
    <el-menu-item index="2-2">2</el-menu-item>
    <el-menu-item index="2-3">3</el-menu-item>
  </el-submenu>
  <el-menu-item index="3"><a href="https://www.ele.me" target="_blank"></a></el-menu-item>
</el-menu>
<div class="line"></div>
<el-menu :default-active="activeIndex2" class="el-menu-demo" mode="horizontal" @select="handleSelect">
  <el-menu-item index="1"></el-menu-item>
  <el-submenu index="2">
    <template slot="title">
 
</template>
    <el-menu-item index="2-1">1</el-menu-item>
    <el-menu-item index="2-2">2</el-menu-item>
    <el-menu-item index="2-3">3</el-menu-item>
  </el-submenu>
  <el-menu-item index="3"><a href="https://www.ele.me" target="_blank"></a></el-menu-item>
</el-menu>
<el-button></el-button>
<el-button type="primary"></el-button>
<el-button type="text"></el-button>
<template>
<el-radio class="radio" v-model="radio" label="1">
    </el-radio>
<el-radio class="radio" v-model="radio" label="2"></el-radio>
</template>
<template>
<el-checkbox-group v-model="checkList">
    <el-checkbox label=" A"></el-checkbox>
    <el-checkbox label=" B"></el-checkbox>
    <el-checkbox label=" C"></el-checkbox>
    <el-checkbox label="" disabled></el-checkbox>
    <el-checkbox label="" disabled></el-checkbox>
</el-checkbox-group>
</template>
<div style="margin-top: 15px;">
  <el-input placeholder="" v-model="input5">
    <el-select v-model="select" slot="prepend" placeholder="">
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
    </el-select>
    <el-button slot="append" icon="search"></el-button>
  </el-input>
</div>
<template>
<el-input-number v-model="num1" @change="handleChange" :min="1" :max="10">
</el-input-number>
</template>
<template>
<el-select v-model="value" placeholder="">
    <el-option v-for="item in options" :label="item.label" :value="item.value">
    </el-option>
</el-select>
</template>
<el-switch
  v-model="value1"
  on-text=""
  off-text="">
</el-switch>
<el-switch
  v-model="value2"
  on-color="#13ce66"
  off-color="#ff4949">
</el-switch>
<el-upload
  action="//jsonplaceholder.typicode.com/posts/"
  list-type="picture-card"
  :on-preview="handlePictureCardPreview"
  :on-remove="handleRemove">
  <i class="el-icon-plus"></i>
</el-upload>
<el-dialog v-model="dialogVisible" size="tiny">
  <img width="100%" :src="dialogImageUrl" alt="">
</el-dialog>
<div class="block">
  <span class="demonstration"></span>
  <el-pagination
    layout="prev, pager, next"
    :total="50">
  </el-pagination>
</div>
<div class="block">
  <span class="demonstration"> 7 </span>
    <el-pagination
      layout="prev, pager, next"
      :total="1000">
    </el-pagination>
  </div><template>
<el-button :plain="true" @click="open">
    </el-button>
</template>
<el-row class="tac">
  <el-col :span="8">
    <h5> icon</h5>
    <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
      <el-submenu index="1">
        <template slot="title">
<i class="el-icon-message"></i>

</template>
        <el-menu-item-group>
          <template slot="title">
 
</template>
          <el-menu-item index="1-1">1</el-menu-item>
          <el-menu-item index="1-2">2</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group title="2">
          <el-menu-item index="1-3">3</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-4">
          <template slot="title">
 4
</template>
          <el-menu-item index="1-4-1">1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="2"><i class="el-icon-menu"></i></el-menu-item>
      <el-menu-item index="3"><i class="el-icon-setting"></i></el-menu-item>
    </el-menu>
  </el-col>
  <el-col :span="8">
    <h5> icon</h5>
    <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" theme="dark">
      <el-submenu index="1">
        <template slot="title">
 
</template>
        <el-menu-item-group title="">
          <el-menu-item index="1-1">1</el-menu-item>
          <el-menu-item index="1-2">2</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group title="2">
          <el-menu-item index="1-3">3</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-4">
          <template slot="title">
 4
</template>
          <el-menu-item index="1-4-1">1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="2"></el-menu-item>
      <el-menu-item index="3"></el-menu-item>
    </el-menu>
  </el-col>
  <el-col :span="8">
    <h5></h5>
    <el-menu mode="vertical" default-active="1" class="el-menu-vertical-demo">
      <el-menu-item-group title="">
        <el-menu-item index="1"><i class="el-icon-message"></i></el-menu-item>
        <el-menu-item index="2"><i class="el-icon-message"></i></el-menu-item>
      </el-menu-item-group>
      <el-menu-item-group title="">
        <el-menu-item index="3"><i class="el-icon-message"></i></el-menu-item>
        <el-menu-item index="4"><i class="el-icon-message"></i></el-menu-item>
      </el-menu-item-group>
    </el-menu>
  </el-col>
</el-row>
<el-steps :space="100" :active="active" finish-status="success">
  <el-step title=" 1"></el-step>
  <el-step title=" 2"></el-step>
  <el-step title=" 3"></el-step>
</el-steps>

<el-button style="margin-top: 12px;" @click="next"></el-button>
<el-collapse v-model="activeNames" @change="handleChange">
  <el-collapse-item title=" Consistency" name="1">
    <div></div>
    <div></div>
  </el-collapse-item>
  <el-collapse-item title=" Feedback" name="2">
    <div></div>
    <div></div>
  </el-collapse-item>
  <el-collapse-item title=" Efficiency" name="3">
    <div></div>
    <div></div>
    <div></div>
  </el-collapse-item>
  <el-collapse-item title=" Controllability" name="4">
    <div></div>
    <div></div>
  </el-collapse-item>
</el-collapse>
  </div>
</template>

<script>
export default {
    data() {
        return {
            activeNames: ['1'],
            active: 0,
            dialogImageUrl: '',
            dialogVisible: false,
            value1: true,
            value2: true,
            activeIndex: '1',
            activeIndex2: '1',
            radio: '1',
            checkList: ['', ' A'],
            input3: '',
            input4: '',
            input5: '',
            select: '',
            num1: 1,
            options: [{
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }, {
                value: '4',
                label: ''
            }, {
                value: '5',
                label: ''
            }],
            value: ''
        };
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        handleChange(value) {
            console.log(value);
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        open() {
            this.$message('');
        },
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        next() {
            if (this.active++ > 2) this.active = 0;
        }
    }
}
</script>

<style lang="css">
.el-select .el-input {
   width: 110px;
 }
 .el-upload-list__item.is-success .el-icon-close, .el-upload__input{
   display: none !important;
 }
</style>



